body {
  background: $white;
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: $line-height-base;
}

a:link,
a:visited {
  &:hover {
    text-decoration: none;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  font-family: $font-family-header;

  &.post-title {
    font-weight: bold;
  }

  &.display-6 {
    font-weight: bold;
  }
}

a:link {
  color: $cyan;

  &:hover {
    color: $light-blue-200;
    text-decoration: underline;
  }
}

iframe {
  aspect-ratio: 16/9;
  width: 100%;
}

.page-wrapper {
  position: relative;
}

.banner {
  background-color: $black;

  p {
    font-size: .875em;
    color: $white;
  }
}

ul {
  li {
    margin-bottom: 0.5rem;
  }
}

img {
  width: 100%;
  height: auto;
}

.section {
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.top {
  padding: 50px 0 0 0;
  margin: 0;
  min-height: 80vh;

  h1 {
    line-height: 1.35;
  }
}

.prx-100vh {
  min-height: 100vh;
}

.prx-90vh {
  min-height: 90vh;
}

.prx-70vh {
  min-height: 70vh;
}

.text-white {
  a {
    color: $ltblue;

    &.btn-primary {
      color: $black;
    }
  }

  .fs-3,
  .fs-4 {
    color: $gray-200;
  }
}

footer.bg-black-diagonal {
  a {
    color: $gray-200;
    text-decoration: none;

    &:hover {
      color: $orange;
    }
  }
}

footer {
  .navbar-brand img {
    max-width: 200px;
  }

  .thumbnail-image {
    max-width: 100px;
    height: auto;
  }

  .iab-badge {
    img {
      width: 120px;
      border-radius: 3px;
    }
  }
}

.post-content .thumbnail-image {
  max-width: 250px;
  height: auto;
}

.blockquote-footer {
  font-size: 1.25rem;
  color: $white;
  margin-top: 1rem;
}

.py-5 {
  padding-top: 5rem !important;
  padding-bottom: 5rem !important;
}

.bg-black-diagonal {
  background-color: $black;
  opacity: 1;
  background: repeating-linear-gradient(
    45deg,
    $gray-900,
    $gray-900 2px,
    $black 2px,
    $black 10px
  );
}

.bg-x {
  background-color: $gray-100;
  background-image: url("/assets/img/prx-mark-outline-white.svg");
  background-repeat: repeat;
}

.bg-orange {
  background-color: $orange;
}

.bg-cyan {
  background-color: $cyan;
}

.bg-blue {
  background-color: $blue;
}

.bg-purple {
  background-color: $purple;
}

.bg-x-blue {
  background-color: $blue;
  background-image: url("/assets/img/prx-mark-outline-blue.svg");
  background-repeat: repeat;

  &.text-white {
    .fs-3,
    .fs-4 {
      color: #fff;
    }
  }
}

.bg-prx-100 {
  background: $navy;
  color: $white;
}

.bg-gray,
.bg-prx-200 {
  background: $navy-900;
  color: $white;
}

.bg-prx-300 {
  background: $blue;
  color: $white;
}

.bg-gray-x {
  background: url(/assets/img/bg-x-half.svg) no-repeat center right;
  fill: $navy;
  background-color: $blue;
  color: $white;
}

.bg-white {
  background-color: $light-blue-100;
}

.bg-navy {
  background-color: $navy;
  color: $white;
}

.bg-black {
  background-color: #222;
}

.bg-white-x {
  background: url(/assets/img/bg-white-x.jpg) no-repeat center right;
  background-color: $light-blue-100;
}

.section-quote {
  position: relative;
  overflow: hidden;
}

.icon-svg {
  img {
    position: relative;
    z-index: 2;
    max-width: 150px;
    width: 100%;
    height: auto;
  }
}

.hero {
  padding: 0;
  position: relative;
  overflow: hidden;
  z-index: 1;

  &.lede {
    padding-top: 10rem;
  }

  .hero-x-content {
    margin-top: 0;
    position: relative;
    padding-top: 0;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-bottom: 0;
    background: rgb(0, 58, 87);
    background: linear-gradient(
      90deg,
      rgba(0, 58, 87, 1) 0%,
      rgba(0, 114, 163, 0.5) 50%,
      rgba(0, 114, 163, 0) 100%
    );

    .hero-content-inner {
      z-index: 5;
    }
  }

  &.cta {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }

  &.cta,
  &.hero-no-x {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgb(0, 114, 163);
      background: linear-gradient(
        90deg,
        rgba(0, 114, 163, 0.8) 0%,
        rgba(0, 114, 163, 0.4) 100%
      );
    }
  }
}

.hero-image {
  top: 0;
  left: 0;
  width: 100%;
  bottom: 0;
  z-index: 0;
  position: absolute;
  background: $navy;
  background: linear-gradient(90deg,rgba(9, 9, 121, 1) 0%, rgba(0, 212, 255, 1) 100%);

  div {
    display: block;
    overflow: hidden;
    position: absolute;
    inset: 0px;
    box-sizing: border-box;
    margin: 0px;
  }

  img {
    position: absolute;
    inset: 0px;
    box-sizing: border-box;
    padding: 0px;
    border: none;
    margin: auto;
    display: block;
    width: 0px;
    height: 0px;
    min-width: 100%;
    max-width: 100%;
    min-height: 100%;
    max-height: 100%;
    object-fit: cover;
  }

  &.post {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgb(0, 114, 163);
      background: linear-gradient(
        90deg,
        rgba(0, 114, 163, 0.8) 0%,
        rgba(0, 114, 163, 0.4) 100%
      );
    }
  }
}

.hero-content {
  display: grid;
  grid-gap: 1rem;
  grid-row: 1 / -1;
  position: relative;
  grid-column: 1 / -1;
  align-content: end;

  padding-right: 0.75rem;
  padding-left: 0.75rem;

  .hero-content-inner {
    display: flex;
    z-index: 1;
    grid-row: 1 / -1;
    flex-direction: column;
  }
}

.section-features {
  h3 {
    font-family: $font-family-base;
    font-weight: normal;
    font-style: italic;
    margin-bottom: 1rem;
  }

  img {
    width: 100%;
    height: 150px;
    margin-bottom: 1rem;
    object-fit: cover;
    object-position: center;
    border: 1px solid $gray-300;
  }
}

.product-image {
  img {
    width: 100%;
    height: auto;
  }
}

.post-content {
  h2,
  h3 {
    margin: 2rem 0 1rem;
  }

  img {
    width: 100%;
    height: auto;
    margin-bottom: 1rem;
  }

  .hero-image {
    img {
      margin-bottom: 0;
    }
  }

  a {
    font-family: $font-family-base;
    font-weight: bold;
    color: $blue;
  }

  blockquote {
    padding: 0 0 0 1rem;
    border-left: 3px solid $orange;
  }
}

.blog-thumbnail {
  img {
    object-fit: cover;
    max-width: 250px;
    max-height: 250px;
  }
}

.card {
  padding: 0;
  background: $navy;

  h3,
  p {
    font-size: 1.1rem;
  }

  li {
    font-size: 1rem;
  }
}

.card-img-top,
.card-img-bottom {
  height: auto;
}

[style*="--aspect-ratio"] > :first-child {
  width: 100%;
}
[style*="--aspect-ratio"] > img {
  height: auto;
}
@supports (--custom:property) {
  [style*="--aspect-ratio"] {
    position: relative;
  }
  [style*="--aspect-ratio"]::before {
    content: "";
    display: block;
    padding-bottom: calc(100% / (var(--aspect-ratio)));
  }
  [style*="--aspect-ratio"] > :first-child {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
  }
}

.post-card {
  &:hover {
    opacity: 0.8;
  }

  .card-body {
    z-index: 1;
    padding: 3rem;
  }
}

.card-img-top {
  img {
    width: 100%;
    height: auto;
  }
}

.list-group-item {
  background-color: $navy;
  color: $white;
}

.post-content {
  p,
  img,
  iframe {
    margin-bottom: 1.5rem;
  }
}

figcaption {
  font-family: $font-family-header;
  font-weight: bold;
}
